import Vue from 'vue'
import Router from 'vue-router'
import OwCheater from '@/components/OwCheater'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'OwCheater',
      component: OwCheater
    }
  ]
})



