<template>
  <div class="ow">
    <el-card class="box-card">
      <div v-for="o in status_info" :key="o" class="text item">{{ o }}</div>
    </el-card>

    <div class="searchB">
      <el-input id="sbox" :inline="true" v-model.lazy="input" placeholder="[搜索框] 请输入要查询的id，只显示结果50行">
      	<template slot="append">
		      <el-button type="info" plain @click="clickBTN">搜索</el-button>
	      </template>
      </el-input>
      <!-- <el-button class="searchBtn" type="primary">搜索</el-button> -->
    </div>
    <el-table :data="tableDataBack" stripe style="width: 100%">
      <el-table-column prop="id" label="id"></el-table-column>
      <el-table-column prop="b_name" label="战网id"></el-table-column>
      <el-table-column prop="reason" label="原因"></el-table-column>
      <el-table-column prop="_pub_time" label="封号日期"></el-table-column>
      <el-table-column prop="created" label="抓取时间"></el-table-column>
    </el-table>
  </div>
</template>

<script>

export default {
  name: "OwCheater",
  data() {
    return {
      input: "",
      status_info: [],
      tableDataBack: []
    };
  },
  created() {
    this.getTotalStatus();
    this.getSearchRes("*");
  },
  // watch: {
  //   input: function(new_key) {
  //     this.getSearchRes(new_key);
  //   }
  // },
  methods: {
    getTotalStatus() {
      var othis = this;
      this.axios({
        method: "get",
        url: "/api/total_status"
      }).then(function(response) {
        othis.status_info = [
          "全部数据量: " + response.data.data.total,
          "上次公布时间: " + response.data.data.last_pub_time,
          "上次公布标题: " + response.data.data.title
        ];
      });
    },

    getSearchRes(key) {
      //防抖限制频率
      var that = this;
      var CancelToken = this.axios.CancelToken;
      var source = CancelToken.source();

      // 取消上一次请求
      this.cancelRequest();
      // var url = "http://ten.sooko.club:9901/api/cheaters/" + key;
      var url = "/api/cheaters/" + key;

      this.axios
        .get(url, {
          cancelToken: new this.axios.CancelToken(function executor(c) {
            that.source = c;
          })
        })
        .then(response => {
          // 在这里处理得到的数据
          that.tableDataBack = response.data.data;
          // console.log(that.tableDataBack);
        })
        .catch(err => {
          if (this.axios.isCancel(err)) {
            console.log("Rquest canceled", err.message); //请求如果被取消，这里是返回取消的message
          } else {
            //handle error
            console.log(err);
          }
        });
    },

    clickBTN() {
      this.getSearchRes(this.input)
    },

    cancelRequest() {
      if (typeof this.source === "function") {
        this.source("终止请求");
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.searchB {
  width: 60%;
  margin: 10px auto;
  /* display:inline */
  /* display: inline-block; */
  /* display: block; */
}

</style>



